import React from 'react';

import './counter.scss';

class Counter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tally: 0,
      up: 0,
      down: 0,
      colorChange: 'black',
    };
  }

  handleIncrement = (event) => {
    this.setState(( ) => {
      return {
        tally: event.tally + 1,
        up: event.up + 1,
        down: event.down,
        colorChange: 'green',
      };
    });
  };

  handleDecriment = (event) => {
    this.setState((priorState) => {
      return {
        tally: priorState.tally - 1,
        up: priorState.up,
        down: priorState.down + 1,
        colorChange: 'red',
      };
    });
  };

  handleSetButton = (event) => {
    this.setState({
      tally: 0,
      up: 0,
      down: 0,
      colorChange: 'black',
    });
  };

  render() {
    return (
      <div>
        <button id="currentTally" className={this.state.colorChange} onClick={this.handleSetButton}>tally: {this.state.tally}</button>
        <div id="counterWrap">
          <div>Up: {this.state.up}</div>
          <div>Down: {this.state.down}</div>
        </div>
        <div id="buttonWrap">
          <button id="likeButton" onClick={this.handleLikeButton}>Up</button>
          <button id="dislikeButton" onClick={this.handleDislikeButton}>Down</button>
        </div>
      </div>
    );
  }
}

export default Counter;